import {
  Action,
  ConvertToPluginActionCreators,
  ConvertToPluginActionPredicates,
} from '@ali/4ever-cangjie';
import type { RefBlockInjectionData } from '../mo/models';

export const SET_REFBLOCK_INJECTION_BY_DOCKEY = Symbol(
  'set_refblock_injection_by_dockey',
);
export const SET_REFBLOCK_INJECTION_BY_KEY = Symbol(
  'set_refblock_injection_by_key',
);

export interface SetRefBlockInjectionByDocKeyPayload {
  data: Partial<RefBlockInjectionData>;
  docKey: string;
  overwrite?: boolean;
}

export interface SetRefBlockInjectionByKeyPayload {
  data: Partial<RefBlockInjectionData>;
  key: string;
  overwrite?: boolean;
}

export interface SetRefBlockInjectionByDocKeyAction {
  type: typeof SET_REFBLOCK_INJECTION_BY_DOCKEY;
  payload: SetRefBlockInjectionByDocKeyPayload;
}

export interface SetRefBlockInjectionByKeyAction {
  type: typeof SET_REFBLOCK_INJECTION_BY_KEY;
  payload: SetRefBlockInjectionByKeyPayload;
}

/**
 * 根据 docKey 设置引用块数据
 * @param docKey
 * @param loading
 * @returns
 */
export function setRefBlockInjectionByDocKey(
  docKey: string,
  data: Partial<RefBlockInjectionData>,
  overwrite = true,
) {
  return {
    type: SET_REFBLOCK_INJECTION_BY_DOCKEY,
    payload: {
      data,
      docKey,
      overwrite,
    } as SetRefBlockInjectionByDocKeyPayload,
  };
}

/**
 * 根据 node key 设置引用块加载态
 * @param key
 * @param loading
 * @returns
 */
export function setRefBlockInjectionByKey(
  key: string,
  data: Partial<RefBlockInjectionData>,
  overwrite = true,
) {
  return {
    type: SET_REFBLOCK_INJECTION_BY_KEY,
    payload: {
      data,
      key,
      overwrite,
    } as SetRefBlockInjectionByKeyPayload,
  };
}

export function isSetRefBlockInjectionByDocKeyAction(
  action: Action,
): action is SetRefBlockInjectionByDocKeyAction {
  return action.type === SET_REFBLOCK_INJECTION_BY_DOCKEY;
}

export function isSetRefBlockInjectionByKeyAction(
  action: Action,
): action is SetRefBlockInjectionByKeyAction {
  return action.type === SET_REFBLOCK_INJECTION_BY_KEY;
}

export const actions = {
  setRefBlockInjectionByDocKey,
  setRefBlockInjectionByKey,
};

export const isActions = {
  isSetRefBlockInjectionByDocKeyAction,
  isSetRefBlockInjectionByKeyAction,
};

declare module '@ali/4ever-cangjie' {
  /** type combination for commands、queries、actions */
  interface PluginActionCreators
    extends ConvertToPluginActionCreators<typeof actions> {}
  interface PluginActionPredicates
    extends ConvertToPluginActionPredicates<typeof isActions> {}
}
